@import "variables";
// sass navbar
.nav {
    background-image: url(../../Design/Assets/png/Group-91.jpg);
    position: sticky;
    top: 0;
    left: 0;
    z-index: 6;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    &__logo {
        // width: 67px;
        // height: 82px;
        img {
            margin: .2rem 0 0 .9rem;
        }
    }
    &__items {
        margin: 2rem 1rem 1rem 0rem;
    }
    &__link {
        padding: 0rem 2.5rem 0rem 0rem;
        text-decoration: none;
        font-size: 1.2rem;
        font-weight: 700;
        &--advice {
            color: #2B2B2C;
        }
        &--general:active {
            pointer-events: none;
            color: #1A519E;
        }
        &--languge {
            background-color: transparent;
            color: #1A519E;
            border-radius: 600px;
            border: #1A519E solid 1px;
            span {
                padding: .7rem 1rem 0rem .6rem;
            }
            img {
                // padding: .5rem 0rem 0rem 0rem;
                margin: 0rem -2rem 0rem 0rem;
            }
        }
    }
}

// res navbar
@media only screen and (max-width: 607px) {
    .nav {
        width: 100%;
    }
    .nav__logo img {
        width: 4rem;
        margin: .2rem 0 0 0 0rem;
    }
    .nav__items {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 2rem 1rem 1rem 2rem;
    }
    &__link {
        padding: 0rem 0.1rem 0rem 0rem;
        font-size: 1rem;
        font-weight: 300;
    }
}

// infos css
.hero {
    &__infos {
        height: 117px;
        line-height: 1.4rem;
        background-color: #F4CF1B;
        text-align: center;
        h3 {
            font-size: 17px;
            font-weight: 700;
            color: #000000;
            padding-top: .5rem;
        }
        p {
            margin-top: .5rem;
            color: #1078AD;
            font-size: 15px;
            font-weight: 600;
        }
    }
    &__test {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin-top: 3rem;
        &--img {
            margin: 1rem 0rem 1rem 1rem;
        }
        &--details {
            text-align: center;
            margin: 1rem 1rem 1rem 1rem;
            font-size: 25px;
            font-weight: 700;
            .heading {
                color: #1078AD;
            }
            img {
                margin-top: 1rem;
            }
            .infos {
                color: #7F7F7F;
                margin-top: 1rem;
            }
            .start {
                padding: 0.6rem 5rem;
                background-color: #1078AD;
                color: white;
                font-size: 27px;
                font-weight: 700;
                border-radius: .5rem;
                margin-top: 2rem;
            }
        }
    }
}

