// recommandation
.recommandation {
    height: 487px;
    background-color: #1078AD;
    position: relative;
    &__heading {
        text-align: center;
        color: #D0E5F0;
        font-size: 35px;
        font-weight: 700;
    }
    &__lists {
        display: block;
        color: #fff;
        font-size: 30px;
        font-weight: 600;
        line-height: 3rem;
        margin-left: 1rem;
        span {
            color: #000;
        }
    }
    &__aboutus {
        float: right;
        position: absolute;
        top: 10rem;
        right: 2rem;
        &--action {
            font-size: 31px;
            font-weight: 700;
            color: #F4CF1B;
            text-align: center;
            margin-bottom: 1.5rem;
        }
        &--phone {
            color: #1078AD;
            font-size: 30px;
            font-weight: 700;
            padding: .5rem 3rem;
            background-color: #F4CF1B;
            margin-bottom: 1.5rem;
        }
        &--link {
            font-size: 27px;
            font-weight: 400;
            text-align: center;
            a {
                text-decoration: none;
                color: #FFFFFF;
                border-bottom: 1px solid #F4CF1B;
            }
        }
    }
}

