// footer
.footer {
    height: initial;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1f;
    grid-template-rows: 1fr 1fr;
    // grid-template-areas: 
    //         ". . logo . ."
    //         "links . contacts . texto";
    &__logo {
        grid-area: logo;
        grid-column-start: 4;
        grid-column-end: 5;
        grid-row: 1;
        margin-top: 2rem;
    }
    &__links {
        grid-area: links;
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row: 2;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        margin-bottom: 2rem;
    }
    &__languge {
        color: #000000;
        margin-bottom: .7rem;
        background-color: transparent;
        border-radius: 600px;
        border: #1A519E solid 1px;
        padding: 0.3rem 0.3rem;
        span {
            padding: .7rem .5rem 0rem .6rem;
            font-size: 19px;
            font-weight: 400;
        }
        img {
            // padding: .5rem 0rem 0rem 0rem;
            margin: 0rem -1rem -0.2rem 0rem;
        }
    }
    &__general {
        color: #1078AD;
        font-size: 24px;
        font-weight: 700;
        margin-bottom: .7rem;
        ;
    }
    &__advice {
        color: #2B2B2C;
        font-size: 24px;
        font-weight: 700;
    }
    &__contacts {
        grid-area: contacts;
        text-align: center;
        grid-column-start: 3;
        grid-column-end: 4;
        grid-row: 2;
        margin-bottom: 2rem;
    }
    &__texto {
        grid-area: texto;
        font-size: 18px;
        font-weight: 400;
        line-height: 30px;
        color: #7F7F7F;
        grid-column-start: 4;
        grid-column-end: 5;
        grid-row: 2;
        padding-left: 6rem;
    }
    &__bittitle {
        color: #1078AD;
        font-size: 27px;
        font-weight: 700;
    }
    &__heading {
        color: #2B2B2C;
        font-weight: 700;
        font-size: 19px;
    }
    &__number {
        color: #369D53;
        font-size: 27px;
        font-weight: 700;
    }
}

